@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&family=Poppins:wght@400;500;600&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
body{
  margin: 0;
  padding: 0;
  background-image: url("../../image/bg.png");
  height: 100vh;
  overflow: hidden;
}
.contaner{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  background: rgba(65, 62, 101, 0.25);
  border-radius: 10px;
  -webkit-backdrop-filter: blur(7px);
  backdrop-filter: blur(10px);
  box-shadow: 0 52px 54px 0 rgba(65, 62, 101, 0.25);
  background-color: rgba(0, 0, 0, 0.44);
  
}
.contaner , h1 , p{
  text-align: center;
  padding: 8px;
  color: white;
  
}
.contaner form{
  padding: 0 40px;
  box-sizing: border-box;
}
form .txt_field{
  position: relative;
  border-bottom: 2px solid #adadad;
  margin: 30px 0;
}
.txt_field input{
  width: 100%;
  padding: 0 5px;
  height: 40px;
  font-size: 16px;
  border: white;

}
.txt_field label{
  position: absolute;
  top: 50%;
  left: 5px;
  color: #adadad;
  transform: translateY(-50%);
  font-size: 16px;
  pointer-events: none;
  transition: .5s;
}
.txt_field span::before{
  content: '';
  position: absolute;
  top: 40px;
  left: 0;
  width: 0%;
  height: 2px;
  background: #2691d9;
  transition: .5s;
}

.pass{
  margin: 5px 0px 15px 0px;
  padding: 5px;
  gap: 3px;
  color: white;
  cursor: pointer;
  text-decoration: none;
  display: flex;
  align-items: center;

}

 .terms{
  color: #fedc5a;
}

input[type="submit"]{
  width: 100%;
  height: 50px;
  border: 1px solid;
  background: #c31a12;
  border-radius: 25px;
  font-size: 18px;
  color: #e9f4fb;
  font-weight: 700;
  cursor: pointer;
  outline: none;
}

